import React from 'react'
import './Addition.css'
import { connect } from 'react-redux'
import { getCars } from '../../../store/order-selectors'
import {
  RadioBtns,
  CheckBoxes,
  DateFrom,
  DateTo,
} from '../../common/Forms/Forms'

const Addition = ({ formik, cars }) => {
  const modelData = cars.find((car) => car.name === formik.values.model)
  const carColors = ['–ª—é–±–æ–π', ...modelData.colors]

  const —ÅolorItems = carColors.map((color) => {
    const colorItem = {}
    colorItem.label = color.charAt(0).toUpperCase() + color.slice(1)
    colorItem.value = color
    colorItem.checked = formik.values.color === color
    return colorItem
  })

  return (
    <div className='addition'>
      <div className='addition__option'>
        <div className='addition__title'>–¶–≤–µ—Ç</div>
        <RadioBtns
          name='color'
          items={—ÅolorItems}
          onChange={formik.handleChange}
        />
      </div>

      <div className='addition__option'>
        <div className='addition__title'>–î–∞—Ç–∞ –∞—Ä–µ–Ω–¥—ã</div>
        <DateFrom formik={formik} />
        <DateTo formik={formik} />
      </div>

      <div className='addition__option'>
        <div className='addition__title'>–¢–∞—Ä–∏—Ñ</div>
        <RadioBtns
          name='rate'
          direction='column'
          items={[
            {
              label: `–ü–æ–º–∏–Ω—É—Ç–Ω–æ, ${Math.ceil(
                (1.5 * modelData.priceMin) / (60 * 24)
              )} ‚ÇΩ/–º–∏–Ω`,
              value: 'minute',
              checked: formik.values.rate === 'minute',
            },
            {
              label: `–ù–∞ —Å—É—Ç–∫–∏, ${Math.ceil(modelData.priceMin)} ‚ÇΩ/—Å—É—Ç–∫–∏`,
              value: 'day',
              checked: formik.values.rate === 'day',
            },
          ]}
          onChange={formik.handleChange}
        />
      </div>

      <div className='addition__option'>
        <div className='addition__title'>–î–æ–ø —É—Å–ª—É–≥–∏</div>
        <CheckBoxes
          direction='column'
          items={[
            {
              label: '–ü–æ–ª–Ω—ã–π –±–∞–∫, 500—Ä',
              value: 'isFullTank',
              checked: formik.values.isFullTank === true,
            },
            {
              label: '–î–µ—Ç—Å–∫–æ–µ –∫—Ä–µ—Å–ª–æ, 200—Ä',
              value: 'isNeedChildChair',
              checked: formik.values.isNeedChildChair === true,
            },
            {
              label: '–ü—Ä–∞–≤—ã–π —Ä—É–ª—å, 1600—Ä',
              value: 'isRightWheel',
              checked: formik.values.isRightWheel === true,
            },
          ]}
          onChange={formik.handleChange}
        />
      </div>
    </div>
  )
}

const mapStateToProps = (state) => ({
  cars: getCars(state),
})

export default connect(mapStateToProps)(Addition)
